import server from '../../server.js';
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore,
	},
	data() {
		return {
			gaodu:0,
			classList:[],
			home:true,
			TabCur:0,
			inputVal:"",
			postList:[],
			userList:[],
			Dindex:[],
			showAppFenxiang:false,
			page:1,
			status: 'loading',
			statusTypes: [{
				value: 'more',
				text: '加载前',
				checked: true
			}, {
				value: 'loading',
				text: '加载中',
				checked: false
			}, {
				value: 'noMore',
				text: '我是有底线的',
				checked: false
			}],
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			weikong:true,
			hotList:[]
		}
	},
	onReady: function() {
		this.getHei()	
	},
	onLoad:function(){
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/post.php?type=getClassList', //仅为示例，并非真实接口地址。
			header: {
				'content-type': 'application/x-www-form-urlencoded',
			},
			success: (res) => {
				this.classList=res.data.data;
				console.log(this.classList);
			}
		});
		this.getDayHot();
	},
	onReachBottom:function(){
		this.page++;
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/post.php?type=searchPosts', //仅为示例，并非真实接口地址。
			data: {
				keyword: this.inputVal,
				count:20,
				page:this.page,
				token:server.token
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
			},
			success: (res) => {
				console.log("————————————帖子列表——————————"+this.page);
				if(res.data.data.length==undefined){
					this.page--;
					this.status ="noMore";
				}else{
					this.postList=this.postList.concat(res.data.data);
					this.status ="loading";
					this.$forceUpdate()
					console.log(this.postList);
				}
				
			}
		});
		
	},
	methods: {
		appFenxiang:function(id,type){
			switch (id){
				case 0:
					this.showAppFenxiang=false
					break;
				case 1:
					uni.setClipboardData({
						data: 'http://share.angeli.top/?postId='+this.postid,
						success: function () {
							console.log('success');
						}
					});
					console.log('复制链接')
					break;
				case 2:
					console.log(this.Dindex.PictureId[0])
					console.log(this.Dindex.Content)
					console.log('http://share.angeli.top/?postId='+this.Dindex.PostsId)
					uni.share({
					    provider: 'weixin',
					    scene: "WXSceneSession",
					    type: 5,
					    imageUrl: this.Dindex.PictureId[0],
					    title: this.Dindex.Content,
					    miniProgram: {
					        id: 'gh_a38adc10b952',
					        path: 'pages/postinfo/postinfo?id='+this.Dindex.PostsId,
					        type: 0,
					        webUrl: 'http://share.angeli.top/?postId='+this.Dindex.PostsId
					    },
					    success: ret => {
					        console.log(JSON.stringify(ret));
					    },
						fail: function (err) {
						    console.log("fail:" + JSON.stringify(err));
						}
					});
					break;
				case 3:
					uni.share({
					    provider: "weixin",
					    scene: "WXSenceTimeline",
					    type: 0,
					    href: 'http://share.angeli.top/?postId='+this.Dindex.PostsId,
					    title: this.Dindex.Content,
					    summary: this.Dindex.Content,
					    imageUrl: this.Dindex.PictureId[0],
					    success: function (res) {
					        console.log("success:" + JSON.stringify(res));
					    },
					    fail: function (err) {
					        console.log("fail:" + JSON.stringify(err));
					    }
					});
					break;
				case 4:
					uni.share({
					    provider: "qq",
					    type: 1,
						href:'http://share.angeli.top/?postId='+this.Dindex.PostsId,
					    summary: this.Dindex.Content,
						title:this.Dindex.Content,
						imageUrl: this.Dindex.PictureId[0],
					    success: function (res) {
					        console.log("success:" + JSON.stringify(res));
					    }, 
					    fail: function (err) {
					        console.log("fail:" + JSON.stringify(err));
					    }
					});
					break;
				default:
					
			}
		},
		sososo:function(event){
			//this.inputVal=event.detail.value;
			this.home=false;
			this.requestSousuo(0)
		},
		getDayHot:function(){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=getDayHot', //仅为示例，并非真实接口地址。
				data:{
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'system':server.system
				},
				success: (res) => {
					this.hotList=res.data.data.data;
					console.log(this.hotList);
				}
			});
		},
		requestSousuo:function(type){
			if(type==0){
				uni.showLoading({
					title: '搜索中..',
					mask:true
				});
				uni.request({
					method:'GET',
					url: 'https://api.angeli.top/post.php?type=searchPosts', //仅为示例，并非真实接口地址。
					data: {
						keyword: this.inputVal,
						count:20,
						page:this.page,
						token:server.token
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'system':server.system
					},
					success: (res) => {
						this.postList=[]
						console.log(res.data.data);
						console.log("————————————帖子列表——————————");
						this.postList=res.data.data;
						console.log(this.postList);
						this.weikong=false
						if(res.data.code!=="1"){
							this.weikong=true
						}
						if(res.data.data==false){
							this.weikong=true
						}
						if(res.data.data.length<21){
							this.status ="noMore";
						}
						this.$forceUpdate();
					},
					complete() {
						uni.hideLoading()
					}
				});
			}else{
				uni.showLoading({
					title: '搜索中..',
					mask:true
				});
				uni.request({
					method:'GET',
					url: 'https://api.angeli.top/user.php?type=search', //仅为示例，并非真实接口地址。
					data: {
						keyword: this.inputVal,
						token:server.token
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'system':server.system
					},
					success: (res) => {
						this.userList=[]
						console.log(res.data.data);
						console.log("————————————搜索用户列表——————————");
						this.userList=res.data.data;
						console.log(this.userList);
						this.weikong=false
						if(res.data.code!=="1"){
							this.weikong=true
						}
						if(res.data.data==false){
							this.weikong=true
						}
						if(res.data.data.length<21){
							this.status ="noMore";
						}
						this.$forceUpdate();
					},
					complete() {
						uni.hideLoading()
					}
				});
			}
			
		},
		getPOST:function(id){
			uni.navigateTo({
				url: '../classPost/classPost?id='+id
			})
		},
		getpostinfo:function(id){
			uni.navigateTo({
				url: '../postinfo/postinfo?id='+id
			});
		},
		getClass:function(id){
			uni.navigateTo({
				url: '../classPost/classPost?id='+id
			})
		},
		Like:function(postid,auid,give,index,zc){
			if(auid==server.userinfo.Auid){
				uni.showToast({
					title: "不能给自己种草",
					position:'bottom',
					icon:'none'
				});
				return;
			}
			
			if(give===true){
				var modea='del'
			}else{
				var modea='add'
			}
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=Like", //仅为示例，并非真实接口地址。
				data: {
					fuid: auid,
					postid:postid,
					mode:modea,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
		
				},
				success: (res) => {
					if(res.data.code=="1"){
						if(modea=='add'){
							this.postList[index].ZhongcaoCount=Number(zc)+1;
							this.postList[index].Give=true;
							uni.showToast({
								title: "种草成功！",
								position:'bottom',
								icon:'none'
							});
						}else{
							this.postList[index].ZhongcaoCount=Number(zc)-1;
							this.postList[index].Give=false;
							uni.showToast({
								title: "取消种草成功！",
								position:'bottom',
								icon:'none'
							});
						}
						
						this.$forceUpdate()
					}else{
						
						uni.showToast({
							title: "种草失败！",
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					
				}
			});
		},
		zan:function(){
			uni.showToast({
				title: "点赞",
				position:'bottom',
				icon:'none'
			});
		},
		caidan:function(resa){
			if(resa.AuthorId==server.userinfo.Auid){
				this.menuList=['分享', '举报','删除帖子']
			}else{
				this.menuList=['分享', '举报']
			}
			uni.showActionSheet({
				itemList:this.menuList,
				success: (res) =>{
					switch(res.tapIndex){
						case 0:
							this.showAppFenxiang=true
							break;
						case 1:
							uni.showToast({
								title:'举报成功',
								position:'bottom',
								icon:'none'
							});
							this.$jubao(resa.PostsId,server.userinfo.Auid,resa.AuthorId,'没有理由');
							break;
						case 2:
							this.$delPost(resa.PostsId);
							break;
						default:
							
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		tabSelect(e) {
			this.TabCur = e
			this.requestSousuo(e)
		},
		input:function(event){
			this.inputVal=event.detail.value;
			this.home=true;
			console.log(this.inputVal)
		},
		getbieren:function(e){
			uni.navigateTo({
				url: '../i/bieren?auid='+e
			})
		},
		getHei(){
			let view = uni.createSelectorQuery().select("#topbox");
			view.fields({
			  size: true,
			  scrollOffset: true
			}, data => {
			  this.gaodu=data.height;
			  console.log("总高度：" + this.gaodu);
			}).exec();
		},
		log:function(e){
			console.log(e)
		},
		showImage(res,c){
			var imageurl=res[c];
			uni.previewImage({
				current:c,
				urls: res,
				longPressActions: {
				itemList: ['保存图片'],
					success: (resa) => {
						
						uni.downloadFile({
						    url: imageurl, 
						    success: (res) => {
								console.log(res)
						        if (res.statusCode === 200) {
						           uni.saveImageToPhotosAlbum({
									   filePath: res.tempFilePath,
									   success: function () {
										  uni.showToast({
										  	title: '已保存',
										  	position:'bottom',
										  	icon:'none'
										  });
									   }
								   });
						        }else{
									uni.showToast({
										title: '保存失败',
										position:'bottom',
										icon:'none'
									});
								}
						    }
						});
						
					},
					fail: function (res) {
						console.log(res.errMsg);
					}    
				}
			})
		},
		adduser:function(uid,index){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=gzORungz', //仅为示例，并非真实接口地址。
				data: {
					uid: uid,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				
				},
				success: (res) => {
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none'
					});
					if(res.data.msg=='关注成功'){
						this.hotList[index].AuId.isGz=true;
					}else{
						this.hotList[index].AuId.isGz=false;
					}
				}
			}); 
		}
	}
}
